.loader {
  width: 75px;
  height: 75px;
  box-sizing: border-box;
  border: 6px solid #362f91;
  border-color: #362f91 rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

